.navbar {
    min-height: 3.25rem;
}

.navbar-item {
    display: block;
    padding: .5rem .75rem;
    position: relative;
}

.navbar-item > img {
    width: 80px;
    height: 75px;
    max-height: 80px;
    max-width: 75px;
    margin-left: 45px;
}

.nav-background {
    background-color: #085FA9 !important;
    color: white;
    background-size: cover;
    top: 0;
    position: fixed;
    width: 100%;
    border-radius: 0 !important;
}

.justify-content {
    width: 100%;
    justify-content: space-between;
}
.navbar-brand, .navbar-tabs {
    align-items: stretch;
    display: flex;
    flex-shrink: 0;
    min-height: 5.25rem;
}

.header-title {
    margin-top: -20px;
}

.header-co-op {
    font-size: 30px;
    font-weight: 500;
    letter-spacing: 2px;
}

.header-mart {
    color: #EB0677;
    font-size: 22px;
    font-weight: 500;
    letter-spacing: 2px;
}
/***************************************************** HOME PAGE *****************************************************/

.homepage {
    height: 100%;
    background-image: url(/static/media/background.d1526141.jpg) no-repeat center center fixed !important;
    /* background-color: red; */
    background: image('../../images/background.jpg');
    background-size: cover;
}

.homepage-card-login {
    margin-top: 10% !important;
    width: 50% !important;
    max-width: 50% !important;
}

.homepage-img-content-coop {
    width: 245px !important;
}

.homepage-welcome, .dashboard-congratulations {
    letter-spacing: 5px;
    font-size: 18px;
    color: #EB0677;
    font-weight: bold;
    line-height: 3rem;
}

.homepage-welcome-sign-up, .homepage-welcome-login {
    letter-spacing: 5px;
    font-size: 18px;
    color: #085FA9;
    font-weight: bold;
    line-height: 3rem;
}

.homepage-welcome-sign-up:hover, .homepage-welcome-login:hover {
    letter-spacing: 5px;
    font-size: 18px;
    color: #EB0677;
    font-weight: bold;
    line-height: 3rem;
}

/***************************************************** DASHBOARD PAGE *****************************************************/

.dashboard-done-test-exam {
    font-size: 18px;
    color: #085FA9;
    line-height: 3rem;
}
.ui.card, .ui.cards>.card {
    width: unset !important;
}

#endingTimeMessage {
    visibility: hidden;
    min-width: 250px;
    margin-left: -125px;
    background-color: #085FA9;
    color: #fff;
    text-align: center;
    border-radius: 2px;
    padding: 16px;
    position: fixed;
    z-index: 1;
    left: 50%;
    bottom: 50%;
    font-size: 17px;
}

#endingTimeMessage.show {
    visibility: visible;
    -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
    animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
    from {bottom: 0; opacity: 0;} 
    to {bottom: 50%; opacity: 1;}
}

@keyframes fadein {
    from {bottom: 0; opacity: 0;}
    to {bottom: 50%; opacity: 1;}
}

@-webkit-keyframes fadeout {
    from {bottom: 50%; opacity: 1;} 
    to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
    from {bottom: 50%; opacity: 1;}
    to {bottom: 0; opacity: 0;}
}

.question-content {
    height: 85vh;
    overflow: auto;
}

.hello-user-test {
    position: absolute;
    top: 36px;
    right: 30px;
}

.hello-user {
    text-transform: uppercase;
}

.question {
    font-weight: bold;
}

.hello-user {
    color: #fff;
    font-size: 20px;
}

.answer-question {
    padding: 0 2rem;
}

.description {
    line-height: 2rem !important;
}

